:root {
  //--navColor: #2F4550;
  --navHeight: 50px;
  --uploadTop: 0px;
  //--primaryColor: #f09d51;
  //--mainWhite: #fff;
  //--mainBlack: #222;
  //--mainGrey: #ececec;
  //--mainSpacing: 0.1rem;
  --mainTransition: all 0.3s linear;
  --galleryBoxWidth: 120px;
}

// It seems that $sass variables are not nestable, so stick to var(--navHeight)'s
$navHeight:56px;

@media only screen and (min-width: 599px){
	:root {
	  --navHeight: 64px;
	  --galleryBoxWidth: 220px;
	  --uploadTop: -5px;
	}
	$navHeight:64px;

	nav, nav .nav-wrapper i, nav a.sidenav-trigger, nav a.sidenav-trigger i {
		line-height: var(--navHeight);
		height: var(--navHeight);
	}

	.cart{
		top:var(--navHeight);
	}

	#Display .gallery-box{
		 width:220px;
		 height:220px;
	}
}

* {padding:0; margin:0; box-sizing: border-box; }

html {
	height:100vh;
	width:100vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	font-size:1.5rem;
	background:darkcyan;
	background:cyan;
	height:100vh;
	max-width:100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  width:100vw
}



#Navbar {
  position: sticky;
  top: 0;
	z-index: 3;

  nav{
		position:sticky;
		top:0;
		z-index:3;
	  //box-shadow:0 8px 17px 2px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);
	  height: var(--navHeight);
	  //line-height: var(--navHeight);

    .container > ul > span > i{
      height: var(--navHeight);
      width: var(--navHeight);
      line-height: var(--navHeight);
      text-align: center;
    }
    .container > ul > span:hover{
      background-color: rgba(0,0,0,0.1);
    }
    .brand-logo {
      left: inherit ! important;
      -webkit-transform: translateX(0) ! important;
      transform: translateX(0) ! important;
      font-size:20px;
      font-weight:bold;
    }
    i {
			font-size:20px;
		}
  }

  .search{
    height:var(--navHeight);
    position:absolute;
    top:0;
    transition:top .2s linear;
    overflow:hidden;
    z-index:0;
    width: 100%;

		.upload{
			background-color: unset !important;
			height: var(--navHeight) !important;
			line-height: var(--navHeight) !important;
			font-size:1rem;

			div{
				height: var(--navHeight) !important;
				line-height: var(--navHeight) !important;
				display: inline-block;
				top:var(--uploadTop);
				margin-right:10px;
				padding: 0 15px;
			}

			div > div {
				margin-right:0;
			}

			div > i{height: var(--navHeight) !important; line-height: var(--navHeight) !important;width: var(--navHeight) !important;}
			div > div:hover, div > i:hover{
				background-color: rgba(0, 0, 0, 0.1);
			}
		}

  }

  .search-open{
    //height:calc(var(--navHeight) + 1.5rem);
    top: calc(var(--navHeight) + 0px);
    z-index:2;
  }

  .search-bar {
    height:var(--navHeight);
    //height:calc(var(--navHeight) + 1.5rem);
    height:var(--navHeight);
    //position:sticky;
    //top:var(--navHeight);
    z-index:2;
    display: grid;
    align-items: center;
    //transition:height .2s;

    .container {
      background:white;
      height:3rem;
      border-radius:5px;
      position:relative;
    }

    .container form{
      padding:0
    }

    .container form button{
      padding:0;
      margin:0;
      position:absolute;
      top:0;
      right:0;
      height: 100%;
      width: var(--navHeight);
      text-align: center;
      background:none;
      border:none;
    }

    .container input, .searchbar-input{
      font-size:1.5rem ! important;
      padding-left:1rem ! important;
      box-sizing:border-box;
      border:none  ! important;
      margin:0  ! important;
    }

    .container button i{
      font-size: 2rem;
    }

  }

}

#Display {
	position:relative;
	top: var(--navHeight);
	top: 0;
	padding-top: 20px;
	padding-bottom: 30px;

	// To push footer to bottom when Display content < 100vh.
	min-height: calc(100vh - var(--navHeight) * 2);

	.gallery-box{
		 width:var(--galleryBoxWidth);
		 height:var(--galleryBoxWidth);
		 border:1px solid grey;
		 // display:inline-block; // << no effect inside a grid
		 //margin-top:1.01rem;
		 //margin-right:1.5rem;
		 //margin-top:.5rem;
		 //margin-right:.5rem;
		 //box-shadow:0 8px 17px 2px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);
		 cursor:pointer;
	}


	#hackerApi{

		font-size:1.3rem;

		button{
			padding:5px 15px;
			border-radius: 5px;
			cursor:pointer;
			margin-left:10px;
		}

		.hackerApi-box{
			width:var(--galleryBoxWidth);
			width:100%;
			//height:var(--galleryBoxWidth);
			//border:1px solid grey;
			// display:inline-block; // << no effect inside a grid
			//margin-top:1.01rem;
			//margin-right:1.5rem;
			//margin-top:.5rem;
			//margin-right:.5rem;
			margin-bottom:15px;
			padding:10px;
			box-shadow:0 8px 17px 2px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);
			background:lightblue;
			overflow: hidden;

			.title{
				color:black;
				font-weight:bold;
			}
			.date, .author{
				color:black;
				font-size: 1.1rem;
			}
			.url{
				color:darkcyan
			}



		}



	}

	#tester{
		text-align:left;
		button{
			padding:10px;
			margin-bottom:10px;
		}
		.readout{
			padding:10px;
			border:2px solid grey;
			min-height: 30vh;
			text-align:left;
		}

	}

	#chilli{
		border: none;
		grid-row-gap: 3rem;

		div {
			cursor: pointer;
			display: grid;
	    justify-items: center;
	    font-size: medium;
		}

		.logo{
			height: calc(var(--galleryBoxWidth) * .4 );
			width: calc(var(--galleryBoxWidth) * .4 );
			background-repeat: no-repeat;
	    background-size: contain;
	    border: none;
		}

		.hckr{background-image:url(./images/hackernews.png);	}
		.rddt{background-image:url(./images/ms-icon-144x144.png); }
		.shpg{background-image:url(./images/cart.png);	}
		.msgg{background-image:url(./images/contentful.png);	border-radius:5px;}
		.rdbio{background-image:url(./images/favicon.ico);	}
		.asst{background-image:url(./images/shopaholic.png);	}
	}

	#comments{

		padding:10px;

		div{
			//padding:10px;
			//margin-bottom:5px;
			//border-bottom:1px solid grey;
			font-size:medium;
		}

		.commentBox {
			padding:10px;
			white-space: pre-wrap;

			.desc{
				padding-left:0px;
				padding-top:2px;
				white-space: pre-wrap;
			}
		}

		.commentBox:nth-child(even){
			//background-color:rgba(136,208,247, .3);
			background-color:whitesmoke;
		}

		.commentBox:nth-child(odd){
			background-color:#A0FEFE;
			box-shadow:0 8px 17px 2px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);
			margin: 2px 0px;
		}

		#newComments{
			position: relative;
			background:whitesmoke;
			margin-bottom:10px;
			textarea{padding:5px;}
			span{position:absolute; right:20px; line-height:54px; height:54px; bottom:10px}
			i{line-height:54px; height:54px}
		}
	}

	#assets{
		display: grid;
    justify-items: center;
    font-size: medium;
    grid-template-columns: repeat(auto-fit, minmax(calc(var(--galleryBoxWidth) * .7 ), 1fr));

		input{display:none}

		.assetBox{
			border:1px solid grey;
			height: calc(var(--galleryBoxWidth) * .4 );
			width: calc(var(--galleryBoxWidth) * .4 );
			cursor: pointer;
			overflow:hidden;
		}


	}

}

#Footer {
  position: relative;
  top: var(--navHeight);
  top: 0;
  width: 100%;
  color: white;
  padding-top: 0;
  font-size: 1.2rem;

  .container{
    line-height: var(--navHeight);

    div{
      display:inline-block;
      //width: max-content;
      //margin:0px 20px;
      margin-left:0
    }

    img{
      width:36px;
      height:26px;
      vertical-align: middle;
    }
  }
}


.container{width:90% ! important;}

.container-center{
	text-align:center;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(var(--galleryBoxWidth), 1fr));
	justify-items: center;
	justify-content: space-evenly;
	align-content: start;
	grid-gap: 1rem;
	width: 100%;
}





.loader {
  border: 6px solid #f3f3f3; /* Light grey */
  border-top: 6px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 30px ! important;
  height: 30px ! important;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}





